.App {
  text-align: center;
}

.game {
  display: flex;
  flex-direction: column; 
  position: absolute; 
  left: 50%;
  transform: translate(-50%, 0);
  margin-top: 50px;
}

.board {
  background-image: url('./images/Board.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 422px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  border: 0px solid rgb(138, 48, 48);
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 64px;
  height: 64px;
  text-align: center;
  width: 64px;
  background:rgba(0, 0, 0,0);
}

.piece {
  border:4px solid black;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  position: absolute;
  transition: all 1s ease-out 0s;
  z-index: 2;
}

.piece.red {
  background: #920707;
  top:448px;
  left:448px;
}

.piece.blue {
  background: #000879;
  top:0px;
  left:0px;
}

.piece.highlight {
  border:4px solid greenyellow;
}

.circle.highlight{
  border:6px solid greenyellow;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  position: absolute;
  transition: all 1s ease-in-out 0s;
  z-index: 1;
}